<template>
  <div>
      <Row type="flex" justify="space-around">
            <Col span="9">
                <Card>
                    <p slot="title"></p>
                    <div class="server_status">
                        <span></span>
                        <Tag type="dot" :color="constants == 0 ? 'green' : 'red'">{{constants == 0 ? '' : ''}}</Tag>
                    </div>
                    <div class="server_status">
                        <span></span>
                        <Tag checkable color="blue">{{release}}</Tag>
                    </div>
                    <div class="server_status">
                        <span>Node.js</span>
                        <Tag color="#495060">{{platform}}</Tag>
                    </div>
                </Card>
            </Col>
            <Col span="11">
                <Card>
                    <p slot="title"></p>
                    <a href="#" slot="extra" @click.prevent="changeInit">
                        <Icon type="ios-loop-strong"></Icon>
                        
                    </a>
                    <Row type="flex" justify="space-between">
                        <Col span="12">
                            <div class="server_status">
                                <span></span>
                                <Tag color="#495060">{{hostname}}</Tag>
                            </div>
                            <div class="server_status">
                                <span></span>
                                <Tag color="#ff9900">{{type}}</Tag>
                            </div>
                            <div class="server_status">
                                <span></span>
                                <Tag color="#19be6b">{{totalmem}}</Tag>
                            </div>
                            <div class="server_status">
                                <span></span>
                                <Tag color="#19be6b">{{freemem}}</Tag>
                            </div>
                        </Col>
                        <Col span="11">
                            <div>
                                <i-circle :percent="percentage" :size="200" :trail-width="4" :stroke-width="5" stroke-linecap="square" stroke-color="#43a3fb">
                                    <div class="demo-Circle-custom">
                                        <h1></h1>
                                        <p></p>
                                        <span>
                                            
                                            <i>{{percentage}}%</i>
                                        </span>
                                    </div>
                                </i-circle>
                            </div>
                        </Col>
                    </Row>
                </Card>
            </Col>
      </Row>
      <Row type="flex" justify="space-around" style="margin-top:4rem;">
          <Col :span="22">
            <Card>
              <p slot="title">CPU</p>
              <div class="cpu_status">
                  <span>CPU</span>
              </div>
              <Table border :columns="columns" stripe :data="cpu"></Table>
            </Card>
          </Col>
      </Row>
  </div>
</template>

<script>
export default {
    data() {
        return{
            constants:null,
            release:null,
            platform:null,
            hostname:null,
            type:null,
            freemem:null,
            totalmem:null,
            percentage:null,
            cpu:[],
            columns: [
                {
                    title: 'CPU',
                    key: 'model',
                },
                {
                    title: 'CPU(GHz)',
                    key: 'speed'
                },
                {
                    title:'CPU[]( user: | nice: | sys: | idle: | irq: )',
                    key:'times'
                }
            ]
        }
    },
    created(){
        this.init();
        console.log(process.env.NODE_ENV);
    },
    methods:{
        init() {
            this.$axios.post('/api/system').then(res=>{
                let { constants,release,platform,hostname,type,freemem,totalmem,percentage,cpu } =res.data;
                [this.constants,this.release,this.platform,this.hostname,this.type,this.freemem,this.totalmem,this.percentage,this.cpu] = [constants,release,platform,hostname,type,freemem,totalmem,percentage,cpu];
                // cpu.forEach((cpu,idx,arr)=>{
                //     var times = cpu.times;
                //     console.log(`cpu${idx}`);
                //     console.log(`${cpu.model}`);
                //     console.log(`${cpu.speed}MHz`);
                //     console.log(`${((1-times.idle/(times.idle+times.user+times.nice+times.sys+times.irq))*100).toFixed(2)}%`);
                // });
            });
        },
        changeInit() {
            this.init();
        }
    }
}
</script>

<style lang="less">
 @import './../../assets/css/admin/index.less';
</style>



