<template>
<div>
	<Row>
		<!--  -->
		<Col span="19">
			<label for="title" class="article"></label>
			<Input v-model="title" size="large" placeholder="" name="title" class="article_title"></Input>

            <label for="title" class="article"></label>
			<Input v-model="des" size="large" placeholder="" name="title" class="article_title"></Input>

			<mavon-editor @change="changeContent" @save="save" class="article_content" v-model="content" fontSize="18px" placeholder="..." style="min-height:600px;" />
			<Button type="warning" class="article_button" @click="submitArticle"></Button>
		</Col>
		<!--  -->
		<Col span="4" offset="1" class="content_right">
			<Card class="card">
				<label for="date" class="article" slot="title"></label>
                <DatePicker @on-change="dateContent" :value="date" type="date" name="date" size="large" class="data_picker" placeholder="Select date" style="width:100%;"></DatePicker>
            </Card>
			<Card class="card">
                <p slot="title"></p>
				<RadioGroup v-model="radio" vertical>
			        <Radio label="Front">
			            <i class="iconfont icon-h5"></i>
			            <span class="list_menu"></span>
			        </Radio>
			        <Radio label="Back">
			            <Icon class="iconfont icon-nodejs"></Icon>
			            <span class="list_menu"></span>
			        </Radio>
			        <Radio label="about-me">
			            <Icon class="iconfont icon-guanyuwomen"></Icon>
			            <span class="list_menu"></span>
			        </Radio>
			    </RadioGroup>
            </Card>
		</Col>
	</Row>
</div>
</template>
<script>
export default {
	data() {
		return {
			title:'',
			content:'',
			htmlContent:'',
			date:FormatDate(new Date()),
			radio:'',
            contentValue:'',
            des:'',
            original:''
		}
	},
    created() {
        this.init();
    },
    mounted() {

    },
	methods:{
        init() {
            let id = this.$route.params.id;
            this.$axios.get('/api/article/update',{params:{id}}).then(res=>{
                let [data] = res.data;
                // console.log(data);
                this.title = data.title;
                this.des = data.des;
                this.content = data.original;
                this.date = data.time;
                this.radio = data.list;
            });
        },
		changeContent(value,render) {
			this.htmlContent = render;
            this.original = value;
		},
		save(value,render) {
			this.htmlContent = render;
            this.original = value;
            console.log(this.contentValue);
			this.submitArticle();
		},
		submitArticle() {
			let param = {
                title:this.title,
				content:this.htmlContent,
				date:this.date,
				des:this.des,
				original:this.original,
				list:this.radio
			}
			if (this.title == '') {
				this.error('','',false);
			}else{
                this.$axios.post(`/api/article/insert${this.radio}`,param).then(res=>{
					console.log(res.data);
				});
			}
		},
		dateContent(val) {
			this.date = FormatDate(val);
		},
		error (title,content,nodesc) {
            this.$Notice.error({
                title: title,
                desc: nodesc ? '' : content
            });
        },
		success(title,content,nodesc) {
			this.$Notice.success({
				title:title,
				desc:nodesc ? '' :content
			});
		}
	}
}
/**/
function FormatDate (strTime) {
    var date = new Date(strTime);
	return `${date.getFullYear()}${date.getMonth()+1}${date.getDate()}`;
}
</script>
<style>
    @import './../../assets/css/admin/article.less';
</style>



